import Vue from 'vue'; //引入vue
//必须实例化 Vue
let vm = new Vue();

const fly=require("flyio")

export const base_url ={url:''}
if (window.plus){
  base_url.url =window.plus.storage.getItem('place_url')
}else{
  base_url.url = window.localStorage.getItem('place_url')
}
// const base_url ='http://citybus.taichung.gov.tw/mwapis/estop-inspect-basedata/v1.0'      //发布


function plus_net_get(url) {
    if (window.plus){
        return new Promise(function (resolve, reject) {
            let XHR = new window.plus.net.XMLHttpRequest();
            XHR.responseType='json'
            XHR.open('GET', url, true);
            XHR.send();

            XHR.onreadystatechange = function () {
                if (XHR.readyState == 4) {
                    if (XHR.status == 200) {
                        try {
                            let response ={statusCode:XHR.status,
                                data:XHR.response} ;

                            resolve(response);
                        } catch (e) {
                            reject(e);
                        }
                    } else {
                        reject(new Error(XHR.statusText));
                    }
                }
            }
        })
    }else{
        return fly.get(url)
    }

}
function test_net_get(url){
  return vm.$http.get(url)
}

export const getStops=({task_id,range,lon,lat,jwt})=>{
  return test_net_get(`${base_url.url}/estop-inspect-basedata/v1.0/task_details?task_id=${task_id}&range=${range}&lon=${lon}&lat=${lat}&jwt=${jwt}`)
}




