import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
import L from 'leaflet'
import {} from  'leaflet.chinatmsproviders'

const providers =L.TileLayer.ChinaProvider.providers

function load_provider() {
    let re=null
    if (window.plus){
        re =window.plus.storage.getItem('map_provider')

    }else{
        re = window.localStorage.getItem('map_provider')
    }
    if (!re){
        re = 'Google.Normal.Map'  //Google.Normal.Map
    }
    return re
}
function set_provider(provider) {
    if (window.plus){
        window.plus.storage.setItem('map_provider',provider)

    }else{
        window.localStorage.setItem('map_provider',provider)
    }
}
const store = new Vuex.Store({
    state:{
        mapProvider:'',
        providerOptions:{}
    },
    mutations:{
        setProvider(state,mapProvider){
            if (mapProvider==''){
                mapProvider=load_provider()
            }
            state.mapProvider = mapProvider
            set_provider(mapProvider)
            var parts = mapProvider.split('.');

            var providerName = parts[0];
            var mapName = parts[1];
            var mapType = parts[2];
            state.providerOptions = {url: providers[providerName][mapName][mapType],
                options:{subdomains :providers[providerName].Subdomains,
                    key:providers[providerName].key}}
        }
    },
    actions:{

    }
})

export default store



